import React, { useContext, useEffect, useRef, useState } from 'react'
import { NavLink, useNavigate } from 'react-router-dom';
import ContextApp from '../Login/Contexto';
import { IReportes } from './IReportes';
import * as Yup from 'yup';
import swal from 'sweetalert';
import { ErrorMessage, Field, Form, Formik, FormikProps } from 'formik';
import axios from 'axios';
import { Circle, MapContainer, Marker, Popup, TileLayer, useMapEvents } from 'react-leaflet';
import L, { LeafletMouseEvent } from 'leaflet';


const AgregarReporteComponent = () => {

    const navegacion = useNavigate();

    const now = new Date();
    const hora = now.toLocaleTimeString();

    const [address, setAddress] = useState("");

    let calle1 = address;

    

    function MapClickHandler() {
        const map = useMapEvents({
            click: (e) => {
                // Obtiene las coordenadas del clic
                const lat = e.latlng.lat;
                const lon = e.latlng.lng;

                // Llama a la API de geocodificación inversa de OpenStreetMap
                fetch(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${lat}&lon=${lon}`)
                    .then((response) => response.json())
                    .then((data) => {
                        // Obtiene la dirección a partir de la respuesta de la API
                        const address = data.address.road;
                        setAddress(address);
                    });
            }
        });
        return null;
    }


    const reporteInicial: IReportes =
    {
        reporte: "",
        calle: "",
        hora: hora,
        frecuencia: ""
    }

    const [reporte, setReporte] = useState<IReportes>(reporteInicial);

    const PostReporte = async (reporte: IReportes) => {
        try {
            let red: IReportes ={
                calle:address,
                reporte:reporte.reporte,
                frecuencia:reporte.frecuencia,
                hora:reporte.hora
            }

            const respuesta = await fetch("https://segu10-001-site1.gtempurl.com/Reportes",
                {
                    method: "post",
                    body: JSON.stringify(red),
                    headers: { "Content-type": "application/json; charset=UTF-8" }
                });
            swal({
                title: "REPORTE REGISTRADA",
                text: "Se a registrado correctamente!",
                icon: "success"
            });
        } catch (error) {
            console.log(error);
        }
    }


    ///////////////////////////////////////////////////////////////////////////////////////////////////////////

    const validaciones = Yup.object({
        reporte: Yup.string().required("El reporte es requerido")
    });

    ///////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    return (
        <>
            <div className="mt-3 mb-2 text-lg text-center">
                <h2>AGREGA TU REPORTE</h2>
            </div>

            <Formik initialValues={reporte} onSubmit={async e => { await PostReporte(e); navegacion("/Bienvenida"); }} validationSchema={validaciones}>
                {(formikProps) => (
                    <Form>
                        <div className="container">
                            <div className="row">
                                <div className="col">
                                    <h4>¿Cuál es el suceso a reportar?</h4>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio"  {...formikProps.getFieldProps("reporte")} value="Accidente de Tráfico" />
                                        <label className="form-check-label" htmlFor="accidente">
                                            Accidente de Tráfico
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Calle en Barranco" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Calle en Barranco
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Banda Delictiva" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Banda Delictiva
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Venta de Drogas" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Venta de Drogas
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Asaltos" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Asaltos
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Asesinatos" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Asesinatos
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Baches en Calle" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Baches en Calle
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Sin Alumbrado" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Sin Alumbrado
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Sin Banqueta" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Sin Banqueta
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Riñas entre Personas" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Riñas entre Personas
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Escaleras en mal estado" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Escaleras en mal estado
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Perros Callejeros" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Perros Callejeros
                                        </label>
                                    </div>
                                    <div className="form-check">
                                        <input className="form-check-input" type="radio" {...formikProps.getFieldProps("reporte")} value="Calle Solida" />
                                        <label className="form-check-label" htmlFor="robo">
                                            Calle Solida
                                        </label>
                                    </div>

                                </div>

                                <div className="col">
                                    <div className='row mb-2'>
                                        <div className='col-md-12'>
                                            <label htmlFor='txtCalle'><h4>Selecciona la calle donde ocurrio</h4></label>

                                            <input
                                                disabled
                                                type="text"
                                                id="txtCalle"
                                                className="form-control"
                                                value={address}
                                            />

                                            <ErrorMessage name="calle">
                                                {mensaje => <span className='text-danger'>{mensaje}</span>}
                                            </ErrorMessage>
                                        </div>
                                        <div className="col-lg-6 col-md-8 col-sm-12 mb-3" style={{ width: "520px", height: "260px" }}>
                                            <MapContainer center={[19.81726, -97.359703]} zoom={15} style={{ width: '100%', height: '100%' }}>
                                                <TileLayer
                                                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                                                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                                                />
                                                <MapClickHandler />
                                                
                                            </MapContainer>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                        <div className='row mb-2 mt-2'>
                            <div className='col-md-12'>
                                <label htmlFor='txtCalle'><h5>Indica la frecuencia del suceso ocurrido</h5></label>
                                <div className='row'>
                                    <div className='col'>
                                        <div className="form-check">
                                            <input className="form-check-input" type="radio" {...formikProps.getFieldProps("frecuencia")} value="Muy Raro" />
                                            <label className="form-check-label" htmlFor="robo">
                                                1. Muy Raro
                                            </label>
                                        </div>
                                    </div>
                                    <div className='col'>
                                        <div className="form-check">
                                            <input className="form-check-input" type="radio" {...formikProps.getFieldProps("frecuencia")} value="Ocasionalmente" />
                                            <label className="form-check-label" htmlFor="robo">
                                                2. Ocasionalmente
                                            </label>
                                        </div>
                                    </div>
                                    <div className='col'>
                                        <div className="form-check">
                                            <input className="form-check-input" type="radio" {...formikProps.getFieldProps("frecuencia")} value="Frecuentemente" />
                                            <label className="form-check-label" htmlFor="robo">
                                                3. Frecuentemente
                                            </label>
                                        </div>
                                    </div>
                                    <div className='col'>
                                        <div className="form-check">
                                            <input className="form-check-input" type="radio" {...formikProps.getFieldProps("frecuencia")} value="Muy Frecuente" />
                                            <label className="form-check-label" htmlFor="robo">
                                                4. Muy Frecuentemente
                                            </label>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>

                        <div className='row mb-2 mt-3'>
                            <div className='text-center col-12'>
                                <button type='button' onClick={(e) => { formikProps.submitForm(); }} className="btn btn-success">Agregar Reporte</button>
                                <NavLink className="btn btn-secondary ms-2" to={"/Bienvenida"}>Volver</NavLink>
                            </div>
                        </div>
                    </Form>
                )}
            </Formik>
        </>
    )
}

const DefaultIcon = L.icon({
    iconUrl: "https://unpkg.com/leaflet@1.6/dist/images/marker-icon.png",
});
L.Marker.prototype.options.icon = DefaultIcon;


export default AgregarReporteComponent