import { Bar } from 'react-chartjs-2'
import { CategoryScale, Chart } from "chart.js";

import { registerables } from 'chart.js'
import { useEffect, useState } from 'react';
import { IReportes } from './IReportes';

Chart.register(...registerables)

const DatoComponent = () => {

    //const [reportes,setReportes] = useState([]);

    /////////////////////////////////////////////////////////

    const [reportes, setReportes] = useState<IReportes[]>([]);
    const [reportesResumidos, setReportesResumidos] = useState<any[]>([]);

    let nom = reportesResumidos.map(ab => ab.calle);
    let can = reportesResumidos.map(ab => ab.cantidadReportes);

    const data = {
        labels: nom,
        datasets: [
            {
                label: 'Cantidad de Reportes por Calle',
                data: can,
                backgroundColor: 'rgba(54, 162, 235, 0.2)',
                borderColor: 'rgba(54, 162, 235, 1)',
                borderWidth: 3,
            },
        ],
    };
    const options = {};


    type ReportesPorCalle = {
        [calle: string]: number;
    }

    interface IReporte {
        calle: string;
        hora: string;
        reporte: string;
        frecuencia: string;
    }

    interface IReportePorCalle {
        calle: string;
        cantidadReportes: number;
    }

    const GetReporte = async () => {
        try {
            const respuesta = await fetch("https://segu10-001-site1.gtempurl.com/Reportes");
            const json = await respuesta.json();
            setReportes(json);

            const reportesPorCalle: { [key: string]: IReporte[] } = reportes.reduce((obj: { [key: string]: IReporte[] }, item: IReporte) => {
                obj[item.calle] = obj[item.calle] || [];
                obj[item.calle].push(item);
                return obj;
            }, {});

            const reportesResumidos = Object.keys(reportesPorCalle).map((key: string) => ({
                calle: key,
                cantidadReportes: reportesPorCalle[key].length,
            }));

            setReportesResumidos(reportesResumidos);

        } catch (error) {
            alert(error);
        }
    }


    useEffect(() => {
        GetReporte();
    })

    ///////////////////////////////////////////////////////////

    return (
        <>
            <div className="mb-3 mt-3 text-lg text-center">
                <h2>GRÁFICA DE RESUMEN DE REPORTES</h2>
            </div>
            <div className='App ms-5' style={{ width: '100%', height: '450px' }} >
                <Bar data={data} options={options} />
            </div>
        </>
    )

}

export default DatoComponent;
