import React, { useState } from 'react'
import { NavLink, useNavigate } from 'react-router-dom';
import { IUsuarios } from './IUsuarios';
import * as Yup from 'yup';
import swal from 'sweetalert';
import { ErrorMessage, Form, Formik } from 'formik';

const RegistrarClienteComponent = () => {

    const navegacion = useNavigate();

    const usuarioInicial: IUsuarios = {
        nombreC: "", nombreU: "", pwd: "",sexo:"", rol: 2
    };

    const [usuario, setUsuario] = useState<IUsuarios>(usuarioInicial);

    const validaciones = Yup.object({
        nombreC: Yup.string().required("El nombre completo es requerido").max(50, "Maximo 50 caracteres"),
        nombreU: Yup.string().required("El nombre usuario es requerido").max(50, "Maximo 50 caracteres"),
        sexo: Yup.string().required("El sexo es requerido").max(1,"Solo un caracter M o F"),
        pwd: Yup.string().required("La contraseña es requerido").min(5, "Agregar 5 caracteres para mayor seguridad")
    });

    const PostUsuario = async (usuario: IUsuarios) => {
        try {

            const respuesta = await fetch("https://segu10-001-site1.gtempurl.com/Usuarios",
                {
                    method: "post",
                    body: JSON.stringify(usuario),
                    headers: { "Content-type": "application/json; charset=UTF-8" }
                });
            swal({
                title: "HAZ SIDO REGISTRADO",
                text: "Ahora podras acceder!",
                icon: "success"
            });
        } catch (error) {
            alert(error);
        }
    }

    return (
        <>
            <Formik initialValues={usuario} onSubmit={async e => { await PostUsuario(e); navegacion("/seguridad") }} validationSchema={validaciones}>
                {(formikProps) => (
                    <Form>
                        <div asp-validation-summary="All" className="text-danger"></div>
                        <div className="row mt-3">
                            <div className="col-xs-12 offset-sm-2 col-sm-8 offset-md-4 col-md-4 offset-lg-4 col-lg-4">
                                <div className="card">
                                    <h4 className="card-header bg-secondary text-dark text-center">Ingrese sus datos</h4>
                                    <div className="card-body">
                                        <div className="row">
                                            <div className="col-12 text-center mt-1 mb-3">
                                                <img src="/seguridad/im2.jpg" className="img-fluid" width="200" alt="sin imagen" />
                                            </div>
                                        </div>
                                        <div className="form-group row">
                                            <div className="col-12">
                                                <label htmlFor='txtNombreC'>Ingrese Nombre Completo</label>
                                                <input type="text" id='txtNombreC' className='form-control'{...formikProps.getFieldProps("nombreC")} placeholder="Ingrese su nombre de completo" />
                                                <ErrorMessage name="nombreC">
                                                    {mensaje => <span className='text-danger'>{mensaje}</span>}
                                                </ErrorMessage>
                                            </div>
                                        </div>
                                        <div className="form-group row">
                                            <div className="col-12 mt-3">
                                                <label htmlFor='txtNombreU'>Nombre Usuario</label>
                                                <input type="text" id='txtNombreU' className='form-control'{...formikProps.getFieldProps("nombreU")} placeholder="Ingrese su nombre de usuario" />
                                                <ErrorMessage name="nombreU">
                                                    {mensaje => <span className='text-danger'>{mensaje}</span>}
                                                </ErrorMessage>
                                            </div>
                                        </div>
                                        <div className="form-group row">
                                            <div className="col-12 mt-3">
                                                <label htmlFor='txtSexoU'>Genero</label>
                                                <input type="text" id='txtSexoU' className='form-control'{...formikProps.getFieldProps("sexo")} placeholder="Ingrese su genero" />
                                                <ErrorMessage name="sexo">
                                                    {mensaje => <span className='text-danger'>{mensaje}</span>}
                                                </ErrorMessage>
                                            </div>
                                        </div>
                                        <div className="form-group row">
                                            <div className="col-12 mt-3">
                                                <label htmlFor='txtPwd'>Contraseña</label>
                                                <input type="password" id='txtPwd' className='form-control'{...formikProps.getFieldProps("pwd")} placeholder="Ingrese su contraseña" />
                                                <ErrorMessage name="pwd">
                                                    {mensaje => <span className='text-danger'>{mensaje}</span>}
                                                </ErrorMessage>
                                            </div>
                                        </div>
                                        <div className='row mb-2 mt-2'>
                                            <div className='text-center col-12'>
                                                <button type='button' onClick={(e) => formikProps.submitForm()} className="btn btn-primary col-12">Registrarme</button>
                                                <NavLink className="btn btn-secondary col-12 mt-2" to={"/seguridad"}>Volver</NavLink>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Form>
                )}
            </Formik>

        </>
    )
}

export default RegistrarClienteComponent