import { Form, Formik } from 'formik';
import React, { useContext, useEffect, useState } from 'react'
import { NavLink, useNavigate } from 'react-router-dom';
import ContextApp from './Contexto';
import { IUsuarios } from './IUsuarios';
import * as Yup from 'yup';
import swal from 'sweetalert';

const LoginComponent = () => {

    const validaciones = Yup.object({
        nombreU: Yup.string().required("El usuario es requerido"),
        pwd: Yup.string().required("La contraseña es requerida"),
    });

    const GetUsuario = async () => {
        try {
            const respuesta = await fetch("https://segu10-001-site1.gtempurl.com/Usuarios/Nombre?nombre="+nom+"&pwd="+pwd);
            const json = await respuesta.json();
            json.nombreU == nom && json.pwd == pwd?
                cambiar(true, nom,json.rol,json.sexo)
            :
                cambiar(false,"",0,"");
        } catch (error) {
            swal({
                title:"VERIFICA",
                text:"Usuario o contraseña incorrectos",
                icon:"warning"
              });
        }
    }

    const { cambiar, bandera, nombre } = useContext(ContextApp);

    const [nom,setNom] = useState<string>("");
    const [pwd,setPwd] = useState<string>("");

    return (
        <>
            <div asp-validation-summary="All" className="text-danger"></div>
            <div className="row mt-3">
                <div className="col-xs-12 offset-sm-2 col-sm-8 offset-md-4 col-md-4 offset-lg-4 col-lg-4">
                    <div className="card">
                        <h4 className="card-header bg-dark text-light text-center">Iniciar Sesión</h4>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-12 text-center mt-3 mb-3">
                                    <img src="/seguridad/im1.png" className="img-fluid" width="200" alt="sin imagen" />
                                </div>
                            </div>
                            <div className="form-group row">
                                <div className="col-12">
                                    <label asp-for="Usuario">Usuario</label>
                                    <input type="text" asp-for="Usuario" className="form-control form-control-md" placeholder="Ingrese su Usuario"
                                        onChange={(e)=>{
                                            setNom(e.currentTarget.value);
                                        }}
                                    >                                        
                                    </input>
                                </div>
                            </div>
                            <div className="form-group row">
                                <div className="col-12 mt-3">
                                    <label asp-for="Contraseña">Contraseña</label>
                                    <input type="password" asp-for="Contraseña" className="form-control form-control-md" placeholder="Ingrese su Contraseña"
                                        onChange={(e)=>{
                                            setPwd(e.currentTarget.value);
                                        }}
                                    >
                                    </input>
                                    
                                </div>
                            </div>
                            <div className="form-gropu row">
                                <div className="col-12 text-center">
                                    <NavLink className="btn btn-primary btn-block col-12 mt-3" to={"/Bienvenida"} onClick={(e) => {GetUsuario();}}>Iniciar Sesión</NavLink>
                                    <NavLink className="btn btn-secondary btn-block col-12 mt-1" to={"/RegistrarCliente"} >Registrarse</NavLink>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default LoginComponent