import React, { useContext } from 'react'
import { NavLink, useNavigate } from 'react-router-dom';
import ContextApp from './Contexto';

const NoAutorizado = () => {

  const {cambiar} = useContext(ContextApp);
  
  return (
    <>
      <h2 className='text-center text-danger mt-3'>No Autorizado</h2>
      <div className='text-center'>
        <NavLink className="btn btn-success ms-1" to={"/seguridad"} onClick={(e) => { cambiar(false,'',0,"")}}>Ir a login</NavLink>
        {/* <NavLink className="btn btn-secondary ms-1" to={"/Bienvenida"}>Volver a Bienvenida</NavLink> */}
      </div>
    </>
  )
}

export default NoAutorizado