import React, { useState } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import ContextApp from './Login/Contexto';
import BienvenidaComponent from './Login/BienvenidaComponent';
import LoginComponent from './Login/LoginComponent';
import RegistrarClienteComponent from './Login/RegistrarClienteComponent';
import MenuComponent from './Inicio/MenuComponent';
import ObtenerUbicacionComponent from './Mapa/ObtenerMapaComponent';
import NoAutorizado from './Login/NoAutorizado';
import AgregarReporteComponent from './Reporte/AgregarReporteComponent';
import ReportesRealizadosComponent from './Reporte/ReportesRealizadosComponent';
import EliminarReporteComponent from './Reporte/EliminarReporteComponent';
import DatoComponent from './Reporte/DatoComponent';

function App() {

  const [nombre,setNombre] = useState<string>('----');
  const [rol,setRol] = useState<number>(0);
  const [bandera, setBandera] = useState<boolean>(false);
  const [sexo, setSexo] = useState<string>("");
  function cambiar(b:boolean,n:string,r:number,s:string){
    setNombre(n);
    setBandera(b);
    setRol(r);
    setSexo(s);
  }


  return (
    <>
     <BrowserRouter>
      <ContextApp.Provider value={{nombre,bandera,rol,sexo,cambiar}}>
      <MenuComponent></MenuComponent>
      <div className='container'>
        <Routes>
          <Route path='/seguridad/' element={<LoginComponent />}></Route>
          <Route path='/RegistrarCliente' element={<RegistrarClienteComponent />}></Route>

          <Route path='/Bienvenida' element={bandera?<BienvenidaComponent />:<LoginComponent/>}></Route>

          <Route path='/datos' element={bandera&&rol==1?<DatoComponent/>:<LoginComponent/>}></Route>
          

          <Route path='/Mapa' element={bandera?<ObtenerUbicacionComponent />:<LoginComponent/>}></Route>

          <Route path='/AgregarReporte' element={bandera?<AgregarReporteComponent/>:<LoginComponent/>}></Route>
          <Route path='/ReportesRealizados' element={bandera&&rol==1?<ReportesRealizadosComponent />:<NoAutorizado/>}></Route>
          <Route path='/EliminarReporte/:idp' element={bandera&&rol==1?<EliminarReporteComponent />:<NoAutorizado/>}></Route>
        </Routes>
      </div>
      </ContextApp.Provider>
    </BrowserRouter>
    </>
  );
}

export default App;
