import React, { useState } from 'react'
import { NavLink, useNavigate, useParams } from 'react-router-dom';
import { IReportes } from './IReportes';
import swal from 'sweetalert';

const EliminarReporteComponent = () => {

    const { idp } = useParams();

    const navegacion = useNavigate();

    const EliminarReporte = async () => {
        try {
            //
            await fetch("https://segu10-001-site1.gtempurl.com/Reportes?id=" + idp,
                {
                    method: "delete"
                });
                swal({
                    title:"REPORTE INVALIDADO",
                    text:"Se a quitado correctamente!",
                    icon:"success"
                  });
            navegacion("/ReportesRealizados");
        } catch (error) {
            alert(error);
        }
    }

    return (
        <>
            <h3 className='text-center text-warning mt-3'>¿Invalidar el Reporte?</h3>
                <div className='text-center mt-3'>
                    <button type='button' onClick={(e) => { EliminarReporte(); }} className="btn btn-warning">Invalidar</button>
                    <NavLink className="btn btn-secondary ms-1" to={"/ReportesRealizados"}>Volver</NavLink>
                </div>
        </>
      )

}

export default EliminarReporteComponent