import React, { useContext, useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom';
import ContextApp from '../Login/Contexto';
import { IReportes } from './IReportes';
import Papa from 'papaparse';

const ReportesRealizadosComponent = () => {

    const [reportes, setReportes] = useState<IReportes[]>([]);
    const [reportesResumidos, setReportesResumidos] = useState<any[]>([]);

    type ReportesPorCalle = {
        [calle: string]: number;
    }

    interface IReporte {
        calle: string;
        hora: string;
        reporte: string;
        frecuencia: string;
    }

    interface IReportePorCalle {
        calle: string;
        cantidadReportes: number;
    }


    const GetReporte = async () => {
        try {
            const respuesta = await fetch("https://segu10-001-site1.gtempurl.com/Reportes");
            const json = await respuesta.json();
            setReportes(json);

            const reportesPorCalle: { [key: string]: IReporte[] } = reportes.reduce((obj: { [key: string]: IReporte[] }, item: IReporte) => {
                obj[item.calle] = obj[item.calle] || [];
                obj[item.calle].push(item);
                return obj;
            }, {});

            const reportesResumidos = Object.keys(reportesPorCalle).map((key: string) => ({
                calle: key,
                cantidadReportes: reportesPorCalle[key].length,
            }));

            setReportesResumidos(reportesResumidos);

        } catch (error) {
            alert(error);
        }
    }

    useEffect(() => {
        GetReporte();
    });

    function exportCSV() {
        // Obtenemos la tabla
        const table = document.getElementById('tabla');

        if (!table) {
            console.error("La tabla no existe");
            return;
        }

        // Convertimos la tabla en un archivo CSV
        const csv = Papa.unparse({
            fields: ["Calle", "Hora", "Reporte", "Frecuencia"],
            data: Array.from(table.querySelectorAll('tbody tr')).map(tr => Array.from(tr.querySelectorAll('td')).map(td => td.textContent!.trim() || ""))
        });

        // Creamos un objeto Blob con el archivo CSV
        const blob = new Blob([csv], { type: 'text/csv;charset=iso-8859-1;' });

        // Creamos un objeto URL para el Blob
        const url = URL.createObjectURL(blob);

        // Creamos un enlace de descarga
        const link = document.createElement("a");
        link.setAttribute("href", url);
        link.setAttribute("download", "reportes.csv");

        // Hacemos clic en el enlace para descargar el archivo
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    }


    return (
        <>

            <div className="mb-3 mt-3 text-lg text-center">
                <h2>REPORTES REALIZADOS</h2>
            </div>
            <button onClick={exportCSV} className='btn btn-success'>Exportar CSV</button>
            <table id="tabla" className="table table-dark table-striped mt-3 text-center col-xs-2 col-sm-4  col-md-6 col-lg-8 mt-3">
                <thead>
                    <tr>
                        <th>Calle</th>
                        <th>Hora</th>
                        <th>Reporte</th>
                        <th>Frecuencia</th>
                        <th className='text-center'>Acciones</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        reportes.map((c, i) => (
                            <tr key={c.id}>
                                <td>{c.calle}</td>
                                <td>{c.hora}</td>
                                <td>{c.reporte}</td>
                                <td>{c.frecuencia}</td>
                                <td className='text-center'>
                                    <NavLink to={`/EliminarReporte/${c.id}`} className='btn btn-warning ms-2'>Invalidar</NavLink>
                                </td>
                            </tr>
                        ))
                    }
                </tbody>
            </table>
            <hr></hr>
            <div className="mb-3 mt-3 text-lg text-center">
                <h2>RESUMEN DE REPORTES</h2>
            </div>
            <table id="tabla" className="table table-dark table-striped mt-3 text-center col-xs-2 col-sm-4  col-md-6 col-lg-8 mt-3">
                <thead>
                    <tr>
                        <th>Calle</th>
                        <th>Cantidad de Reportes</th>
                    </tr>
                </thead>
                <tbody>
                    {reportesResumidos.map((c, i) => (
                        <tr key={i}>
                            <td>{c.calle}</td>
                            <td>{c.cantidadReportes}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </>
    )
}

export default ReportesRealizadosComponent