import React, { useContext } from 'react'
import { NavLink } from 'react-router-dom'
import ContextApp from '../Login/Contexto';

const MenuComponent = () => {

    const { cambiar, bandera, nombre, rol } = useContext(ContextApp);

    const now = new Date();
    const fecha = now.toLocaleDateString();

    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark justify-content-between" >
            <div className="container-fluid">
                <a className="navbar-brand">Ciudad+Segura</a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    {bandera ?
                        bandera && rol == 1 ?
                            <ul className="navbar-nav">
                                <li className="nav-item">
                                    <NavLink className="nav-link" aria-current="page" to="/Bienvenida">Nivel Peligro</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" aria-current="page" to="/Mapa">Mapa</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" aria-current="page" to="/AgregarReporte">Generar Reporte</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" aria-current="page" to="/ReportesRealizados">Reportes Realizados</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" aria-current="page" to="/datos">Gráfica</NavLink>
                                </li>
                                <li className="nav-item ms-5"></li>
                                <li className="nav-item ms-5"></li>
                                <li className="nav-item ms-5"></li>
                                <li className="nav-item ms-5"></li>
                                <li className="nav-item ms-1">
                                    <button className='btn btn-secondary text-uppercase'
                                    >FECHA: {fecha}</button>
                                </li>
                                <li className="nav-item ms-1">
                                    <button className='btn btn-success text-uppercase'
                                    >USUARIO: {nombre}</button>
                                </li>
                                <li className="nav-item ms-1">
                                    <NavLink to={'/seguridad/'} className='btn btn-danger' onClick={(e) => {
                                        cambiar(false, "", 0, "");
                                    }}>Cerrar Sesión</NavLink>
                                </li>
                            </ul>
                            :
                            bandera && rol == 2 ?
                                <ul className="navbar-nav">
                                    <li className="nav-item">
                                        <NavLink className="nav-link" aria-current="page" to="/Bienvenida">Bienvenida</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" aria-current="page" to="/Mapa">Mapa</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" aria-current="page" to="/AgregarReporte">Generar Reporte</NavLink>
                                    </li>
                                    <li className="nav-item ms-3"></li>
                                    <li className="nav-item ms-5"></li>
                                    <li className="nav-item ms-5"></li>
                                    <li className="nav-item ms-5"></li>
                                    <li className="nav-item ms-5"></li>
                                    <li className="nav-item ms-5"></li>
                                    <li className="nav-item ms-5"></li>
                                    <li className="nav-item ms-5"></li>
                                    <li className="nav-item ms-5"></li>
                                    <li className="nav-item ms-5"></li>
                                    <li className="nav-item ms-1">
                                        <button className='btn btn-secondary text-uppercase'
                                        >FECHA: {fecha}</button>
                                    </li>
                                    <li className="nav-item ms-1">
                                        <button className='btn btn-success text-uppercase'
                                        >USUARIO: {nombre}</button>
                                    </li>
                                    <li className="nav-item ms-1">
                                        <NavLink to={'/seguridad'} className='btn btn-danger' onClick={(e) => {
                                            cambiar(false, "", 0, "");
                                        }}>Cerrar Sesión</NavLink>
                                    </li>
                                </ul>
                                :
                                <ul className="navbar-nav">
                                    <li className="nav-item">
                                        <NavLink className="nav-link" aria-current="page" to="/seguridad1/">Iniciar Sesión</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" aria-current="page" to="/RegistrarCliente">Registrarse</NavLink>
                                    </li>
                                </ul>
                        :
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <NavLink className="nav-link" aria-current="page" to="/seguridad/">Iniciar Sesión</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" aria-current="page" to="/RegistrarCliente">Registrarse</NavLink>
                            </li>
                        </ul>
                    }
                </div>
            </div>
        </nav>


    )
}

export default MenuComponent

function useContenxt(ContextApp: React.Context<{ nombre: string; bandera: boolean; cambiar(bandera: boolean, nombre: string): void }>): { bandera: any } {
    throw new Error('Function not implemented.')
}